import React from "react";
import { useDispatch, useSelector } from "react-redux";
import Slider from "react-slick";
import { deleteProperty, UserApi } from "../../rtk/slices/UserSlice";
import toast from "react-hot-toast";

const PropertyController = ({ settings }) => {
  const { user,status } = useSelector((state) => state.user);

  /* for delete property */
  const dispatch = useDispatch();

  const deleteHandler = async (id) => {
    await dispatch(deleteProperty(id));
    toast.success("Property Deleted...")
    dispatch(UserApi());
  };

  return (
    <>
      <div className="container-fluid py-5">
        {status === "Loading" && <h2>Loading...</h2>}
        {status === "Success" && <h1>Welcome to Investment Reality Group</h1>}
        {user &&
          user.map((items, index) => {
            return (
              <>
                <div className="row justify-content-center mb-5" key={index}>
                  <div className="col-md-8 col-10 mt-5">
                    <div class="card">
                      {items.image.length <= 1 && !items.video ? (
                        <div className="image-container" key={index}>
                          <button
                            type="button"
                            className="btn"
                            aria-label="Close"
                            onClick={() => deleteHandler(items._id)}
                          >
                            <i class="fas fa-trash px-2"></i>Delete Property
                          </button>

                          <img
                            class="card-img-top"
                            src={items.image}
                            alt=" Not Found"
                            height={400}
                          />
                        </div>
                      ) : (
                        <Slider {...settings}>
                          {items.video && (
                            <video
                              src={items.video}
                              controls
                              height={400}
                            ></video>
                          )}
                          {items.image.map((propertyImages, index) => (
                            <div className="image-container" key={index}>
                              <button
                                type="button"
                                className="btn"
                                aria-label="Close"
                                onClick={() => deleteHandler(items._id)}
                              >
                                <i class="fas fa-trash px-2"></i>Delete Property
                              </button>

                              <img
                                class="card-img-top"
                                src={propertyImages}
                                alt=" Not Found"
                                height={400}
                              />
                            </div>
                          ))}
                        </Slider>
                      )}
                      <div className="details d-flex">
                        <div class="card-body border ">
                          <h6 class="card-text">{items.city}</h6>
                          <h6 class="card-text">{items.landmark}</h6>
                          <h6 class="card-text">{items.desc}</h6>
                        </div>
                        <div class="card-body">
                          <h6 class="card-text">{`${items.price}`}</h6>
                          <h6 class="card-text">{items.propertyStatus}</h6>
                          <h6 class="card-text">{items.propertyType}</h6>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </>
            );
          })}
      </div>
    </>
  );
};

export default PropertyController;
