import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import toast from "react-hot-toast";

axios.defaults.withCredentials = true
/* Admin */
/* export const registerAdmin=createAsyncThunk("admin/register-admin",async(registrationForm)=>{
    const response=await axios.post(`api/auth/register-admin`)
    return response.data
}) */

export const loginAdmin = createAsyncThunk(
  "admin/login-admin",
  async (loginForm) => {
    const response = await axios.post(`https://pro-w9g7.onrender.com/api/auth/login-admin`, loginForm);
    if (response.status === 200) {
      toast.success("Login Successfully")
      localStorage.setItem("AdminLogin", "true")
      return response.data
    }


  }
);
export const logoutAdmin = createAsyncThunk("admin/logout-admin", async () => {
  const response = await axios.post(`https://pro-w9g7.onrender.com/api/auth/logout-admin`);
  if (response.status === 200) {
    localStorage.removeItem("AdminLogin")
    toast.success("Logout Successfully")

    return response.data;
  }

});

/* user  */
export const signupUser = createAsyncThunk("user/sign-up", async (formdata) => {
  const response = await axios.post(`https://pro-w9g7.onrender.com/api/auth/register-user`, formdata)
  if (response.status === 201) {
    toast.success("Sign Up Successfully")
    return response.data
  }

})
export const loginUser = createAsyncThunk("user/login", async (formdata) => {
  const response = await axios.post(`https://pro-w9g7.onrender.com/api/auth/login-user`, formdata)

  if (response.status === 200) {
    toast.success("Login Successfully")
    localStorage.setItem("UserLogin", "true")
    return response.data.result
  }

})
export const logoutUser = createAsyncThunk("user/logout", async () => {
  const response = await axios.post(`https://pro-w9g7.onrender.com/api/auth/logout-user`)

  if (response.status === 200) {

    localStorage.removeItem("UserLogin")
    toast.success("Logout Successfully")
    return response.data
  }

})
const AuthSLice = createSlice({
  name: "auth",
  initialState: {
    isLogin: null,
    admin: [],
  },
  reducers: {},
  extraReducers: (builder) => {
    builder.addCase(loginAdmin.fulfilled, (state, action) => {
      state.isLogin = true
      state.admin.push(action.payload)
    });
    builder.addCase(logoutAdmin.fulfilled, (state, action) => {
      state.isLogin = false
      state.admin = [];
    });
  },
});
export default AuthSLice.reducer;
