import React, { useEffect } from 'react';
import {  getProperty } from '../../rtk/slices/UserSlice';
import { useDispatch } from 'react-redux';
import PropertyController from './PropertyController';



const Substainence = () => {
  const dispatch = useDispatch();


  useEffect(() => {
    dispatch(getProperty('substainence'));
  }, [dispatch]);

  
  return (
    <>
    <PropertyController/>
    </>
  );
};

export default Substainence;