import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";




const url = "https://pro-w9g7.onrender.com";

/* getAllProperty */
export const UserApi = createAsyncThunk("user/getAll-Property", async () => {
  const response = await axios.get(`${url}/api/user/getAll-property`);
  return response.data.result;
});


/* getProperty */
export const getProperty = createAsyncThunk("user/getProperty", async (formData) => {
  try {

    if (formData.type === "Buy") {

      if (formData.city && formData.landmark && formData.propertyStatus && formData.propertyType) {
        const response = await axios.get(`${url}/api/user/get-property?city=${formData.city}&landmark=${formData.landmark}&propertyStatus=${formData.propertyStatus}&propertyType=${formData.propertyType}`)
        return response.data


      }
      else if (formData.city) {
        const response = await axios.get(`${url}/api/user/get-property?city=${formData.city}`)

        return response.data;
      }
      else if (formData.landmark) {
        const response = await axios.get(`${url}/api/user/get-property?landmark=${formData.landmark}`)
        return response.data;
      }
      else if (formData.propertyStatus) {
        const response = await axios.get(`${url}/api/user/get-property?propertyStatus=${formData.propertyStatus}`)
        return response.data;
      }
      else if (formData.propertyType) {
        const response = await axios.get(`${url}/api/user/get-property?propertyType=${formData.propertyType}`)
        return response.data;
      }




    }
    else {
      if (formData.city && formData.landmark && formData.propertyStatus && formData.budget) {
        const response = await axios.get(`${url}/api/user/get-property?city=${formData.city}&landmark=${formData.landmark}&propertyStatus=${formData.propertyStatus}&budget=${formData.budget}`)
        return response.data
      }
      else if (formData.city) {
        const response = await axios.get(`${url}/api/user/get-property?city=${formData.city}`)
        return response.data;
      }
      else if (formData.landmark) {
        const response = await axios.get(`${url}/api/user/get-property?landmark=${formData.landmark}`)
        return response.data;
      }
      else if (formData.propertyStatus) {
        const response = await axios.get(`${url}/api/user/get-property?propertyStatus=${formData.propertyStatus}`)
        return response.data;
      }
      else if (formData.budget) {
        const response = await axios.get(`${url}/api/user/get-property?budget=${formData.budget}`)
        return response.data;
      }
    }


  } catch (error) {
    console.log("Error occurred:", error);
    throw error; // Re-throw the error to handle it in the calling code
  }
});

/* addProperty */
export const addProperty = createAsyncThunk(
  "user/app-property",
  async (PropertyData) => {
    try {
      const response = await axios.post(
        `${url}/api/admin/add-property`,
        PropertyData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }
      );
      return response.data;
    } catch (error) {
      console.error(error);
      throw error;
    }
  }
);

/* delete property*/
export const deleteProperty = createAsyncThunk("user/delete", async (id) => {

  try {
    const response = await axios.delete(`${url}/api/admin/delete-property/${id}`)
    return response.data
  } catch (error) {
    console.log("delete api error", error)
  }
})

const UserSlice = createSlice({
  name: "user",
  initialState: {
    user: [],
    status: null,
    error: null,
  },
  reducers: {

  },
  extraReducers: (builder) => {

    builder.addCase(UserApi.pending, (state, action) => {
      state.status = "Loading"
    }).addCase(UserApi.fulfilled, (state, action) => {
      state.status = "Success"
      state.user = action.payload
    });

    /* get property */
    builder
      .addCase(getProperty.pending, (state, action) => {

        state.status = "Loading"


      }).addCase(getProperty.fulfilled, (state, action) => {

        state.status = "Success"
        state.user = action.payload

      })
      .addCase(getProperty.rejected, (state, action) => {

        state.status = "Failed"
        state.error = action.error.message

      })

    /* add Property  */
    builder
      .addCase(addProperty.pending, (state, action) => {
        state.status = "Loading"
      })
      .addCase(addProperty.fulfilled, (state, action) => {

        state.status = "Success";
        state.user = action.payload
      })
      .addCase(addProperty.rejected, (state, action) => {
        state.status = "Error";
      });


  },
});
export default UserSlice.reducer;
