import React, { useState } from "react";
import toast from "react-hot-toast";
import { useDispatch, useSelector } from "react-redux";
import { addProperty } from "../rtk/slices/UserSlice";

const AddProperty = ({indianCities}) => {
  const [adminFormData, setAdminFormData] = useState({
    city: "",
    price: "",
    landmark: "",
    desc: "",
    preselection: "",
    propertyStatus: "",
    propertyType: "",
    budget:""
  });

  const [img, setImage] = useState([]);
  const [video, setVideo] = useState(null);
  const dispatch = useDispatch();
  const { status } = useSelector((state) => state.user);

  const handleChangeAdminFormData = (e) => {
    setAdminFormData({
      ...adminFormData,
      [e.target.name]: e.target.value,
    });

  };

  const HandleAdminSubmit = async (e) => {
    e.preventDefault();
   

    try {
      const formData = new FormData();
      formData.append("city", adminFormData.city.toLowerCase());
      formData.append("price", adminFormData.price);
      formData.append("landmark", adminFormData.landmark.toLowerCase());
      formData.append("desc", adminFormData.desc.toLowerCase());
      formData.append("preselection", adminFormData.preselection.toLowerCase());
      formData.append("propertyStatus", adminFormData.propertyStatus.toLowerCase());
      formData.append("propertyType", adminFormData.propertyType.toLowerCase());
      formData.append("budget", adminFormData.budget.toLowerCase());
   
      
      for (let i = 0; i < img.length; i++) {

        formData.append(`image`, img[i]);
  
      }
      
   
      
      formData.append("video", video);
      
      await dispatch(addProperty(formData));

      toast.success("Property Added Successfully...");

      setAdminFormData({
        city: "",
        price: "",
        landmark: "",
        desc: "",
        preselection: "",
        propertyStatus: "",
        propertyType: "",
        budget:""
        
      });
    
    } catch (error) {
      toast.error("Data Not Submitted");
    }
  };

  return (
    <div>
      {/* Admin Form */}
      {status === "Loading" && <h5 className="mt-5">Loading....</h5>}
      <div className="container my-5">
        <div className="row justify-content-center">
          
          <div className="col-5 text-start">
            <form action="" onSubmit={HandleAdminSubmit}>
              <div className="form-group">
                <input type="radio" name="sell" defaultChecked required />{" "}
                &nbsp;
                <label htmlFor="sell">Sell</label>
              </div>

              <div className="form-group my-3">
                <input
                  type="text"
                  name="price"
                  value={adminFormData.price}
                  placeholder="Price"
                  className="form-control"
                  onChange={handleChangeAdminFormData}
                  required
                />
              </div>
              <div className="form-group my-3">
                  <select
                    name="budget"
                    id="select"
                    className="form-control"
                    value={adminFormData.budget}
                    onChange={handleChangeAdminFormData}
                  >
                    <option value="">Select Budget</option>
                    <option value="52lakh to 60lakh">
                      52 Lakh to 60 Lakh
                    </option>
                    <option value="1cr to 2cr">1cr to 2cr</option>
                    <option value="2cr">2cr+</option>
                  </select>
                </div>
              <div className="form-group my-3">
                <select
                  name="city"
                  id=""
                  value={adminFormData.city}
                  className="form-control "
                  onChange={handleChangeAdminFormData}
                  required
                >
                  <option value="">Select City</option>
                  {
                    indianCities.map((cities,index)=>(<option value={cities.toLowerCase()} key={index}>{cities}</option>))
                  }
                </select>
              </div>

              <div className="form-group my-3">
                <input
                  type="text"
                  name="landmark"
                  value={adminFormData.landmark}
                  placeholder="Landmark or Locality"
                  className="form-control"
                  onChange={handleChangeAdminFormData}
                  required
                />
              </div>
              <div className="form-group my-3">
                <input
                  type="text"
                  name="desc"
                  value={adminFormData.desc}
                  placeholder="Description"
                  className="form-control"
                  onChange={handleChangeAdminFormData}
                  required
                />
              </div>
              <div className="form-group my-3">
                <select
                  name="preselection"
                  id=""
                  className="form-control "
                  onChange={handleChangeAdminFormData}
                  required
                >
                  <option value="">Preselection</option>
                  <option value="residential">Residential</option>
                  <option value="row-house">Row House</option>
                  <option value="villa">Villa</option>
                  <option value="commercial">Commercial</option>
                </select>
              </div>
              <div className="form-group my-3">
                <select
                  name="propertyStatus"
                  id=""
                  className="form-control "
                  onChange={handleChangeAdminFormData}
                  required
                >
                  <option value="">Property Status</option>
                  <option value="pre-launch">Prelauch</option>
                  <option value="launch">Launch</option>
                  <option value="under-construction">Under Construction</option>
                  <option value="substainence">Substainence</option>
                  <option value="nearby-possession">Nearby Possession</option>
                  <option value="ready-to-move">Ready to Move</option>
                  <option value="resale">Resale</option>
                </select>
              </div>
              {adminFormData.preselection !== "Commercial" && (
                <div className="form-group my-3">
                  <select
                    name="propertyType"
                    id=""
                    className="form-control "
                    onChange={handleChangeAdminFormData}
                    required
                  >
                    <option value="">Property Type</option>
                    <option value="1bhk">1BHK</option>
                    <option value="1.5bhk">1.5BHK</option>
                    <option value="2bhk">2BHK</option>
                    <option value="2.5bhk">2.5BHK</option>
                    <option value="3bhk">3BHK</option>
                    <option value="3.5bhk">3.5BHK</option>
                    <option value="4bhk">4BHK</option>
                    <option value="simplex">Simplex</option>
                    <option value="duplex">Duplex</option>
                    <option value="row-House">Row House</option>
                    <option value="villa">Villa</option>
                    <option value="commercial">Commercial</option>
                  </select>
                </div>
              )}
              <div className="form-group my-3">
                <label htmlFor="image">Upload Image</label>
                <input
                  type="file"
                  name="image"
                  accept="image/*"
                  multiple

                  className="form-control my-2"
                  onChange={(e)=>setImage([...img, ...e.target.files])}
                />
              </div>
              <div className="form-group my-3">
                <label htmlFor="video">Upload Video</label>
                <input
                  type="file"
                  name="video"
                  accept="video/*"
                  
                  className="form-control my-2"
                  onChange={(e) => setVideo(e.target.files[0])}
              
                />
              </div>

              <button className="btn btn-primary" type="submit">
                Submit
              </button>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AddProperty;
